import React, { Component } from "react";
import { STUDIO_URL } from "../../actions/api";

let wallEditor = document.getElementsByClassName("studio-wall");
class Studio extends Component {
  constructor(props) {
    super(props);

    this.state = {
      loading: false,
    };
  }

  componentDidMount() {
    this.onLoad();
  }

  onLoad = () => {
    var container = document.getElementById("root");
    this.setState({ themeSettingStatus: false });

    if (!this.state.loading && wallEditor.length) {
      this.setState({ loading: true });

      if (container)  
        setTimeout(() => this.wallLoad());
       else this.wallLoad();
    }
  };

  wallLoad = () => {

    const url = STUDIO_URL;
    window.wall_Id = this.props.wall.Wall.id;

    fetch(url)
      .then((response) => response.json())
      .then((json) => {
        const { id, script, css } = json;
        if (
          wallEditor.length == 0 &&
          window.location.pathname.includes("wall/personalize")
        ) {
          setTimeout(() => this.wallLoad(), 1000);
          return;
        }

        let myScript = document.createElement("div");
        myScript.setAttribute("id", id);
        document.getElementsByClassName("studio-wall")[0].appendChild(myScript);

        let myCss = document.createElement("link");
        myCss.setAttribute("type", "text/css");
        myCss.setAttribute("rel", "stylesheet");
        myCss.setAttribute("href", css);
        document.head.appendChild(myCss);

        let myreact = document.createElement("script");
        myreact.setAttribute("src", script);
        document.body.appendChild(myreact);

        setTimeout(() => this.setState({ loading: false }), 1000);
      });
  };

  render() {
    return (
      <div style={{ display: "flex" }}>
        <div className={`studio-wall`}></div>
      </div>
    );
  }
}

export default Studio;
